* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: 'MoveSansBold';
  src: url('./assets/fonts/MoveSans-Bold.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'MoveSansLight';
  src: url('./assets/fonts/MoveSans-Light.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'MoveSansMedium';
  src: url('./assets/fonts/MoveSans-Medium.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Dovemayo';
  src: url('./assets/fonts/Dovemayo_wild.ttf');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Cafe24Surround';
  src: url('./assets/fonts/Cafe24Ssurround-v2.0.ttf');
  font-weight: normal;
  font-style: normal;
}

.movesansBold {
  font-family: 'MoveSansBold', sans-serif;
}

.movesansLight {
  font-family: 'MoveSansLight', sans-serif;
}

.movesansWild {
  font-family: 'MoveSansMedium', sans-serif;
}

.dovemayo {
  font-family: 'Dovemayo', sans-serif;
}

.cafeSurround {
  font-family: 'Cafe24Surround', sans-serif;
}

.introScreenContainer {
  background-color: rgb(244, 233, 233);
}