.infoContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    padding-top: 5rem;
}

.titleContainer {
    margin-bottom: auto;
}

.nameLogo {
    font-family: 'movesansBold';
    font-size: 35px;
    padding: 1rem;
}

.infoContainerWrapper {
    display: flex;
    flex: 1;
    width: 100%;
    justify-content: center;
    align-items: center;
}

.infoContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
}

.infoContent1 {
    font-size: 150px;
    font-family: 'movesansBold';
    letter-spacing: 15px;
    line-height: 1.2;
}

.infoContent2 {
    font-size: 150px;
    font-family: 'movesansLight';
    letter-spacing: 15px;
    line-height: 1.45;
}

.infoMyName {
    letter-spacing: 15px;
    font-family: 'Cafe24Surround';
}

.aboutScreenContainer {
    height: 120vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.aboutMeTitle {
    font-family: 'Cafe24Surround';
    font-size: 30px;
}

.aboutMeWrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.profilePicture {
    width: 300px;
    height: auto;
    border-radius: 300px;
}

.introduceMyself {
    font-family: 'dovemayo';
    font-size: 20px;
}

.skillScreenContainer {
    height: 100vh;
    display: flex;
    justify-content: center;
}

.skillTitle {
    font-family: 'Cafe24Surround';
    font-size: 35px;
}

.skillDetailContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.publishingContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.publishingTitle {
    font-family: 'Cafe24Surround';
    font-size: 25px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.publishingImage {
    display: flex;
    flex-direction: row;
}

.javaScriptImage {
    width: 120px;
    height: 120px;
    border-radius: 10px;
    margin-right: 10px;
}

.htmlImage {
    width: 120px;
    height: 120px;
    margin-right: 10px;
}

.cssImage {
    width: 120px;
    height: 120px;
    margin-right: 10px;
}

.frontendTitle {
    font-family: 'Cafe24Surround';
    font-size: 25px;
    padding-top: 40px;
    padding-bottom: 20px;
}

.vueImage {
    width: 120px;
    height: 120px;
    margin-right: 10px;
}

.reactImage {
    width: 122px;
    height: 120px;
    border-radius: 20px;
    margin-right: 10px;
}

.typeImage {
    width: 120px;
    height: 120px;
    border-radius: 20px;
}

.androidTitle {
    font-family: 'Cafe24Surround';
    font-size: 22px;
    padding-top: 40px;
    padding-bottom: 20px;
}

.androidImage {
    width: 120px;
    height: 120px;
    border-radius: 10px;
    margin-right: 10px;
}

.kotlinImage {
    width: 120px;
    height: 120px;
    border-radius: 10px;
}